let essentials = {
    // Create a new ajax request
    getAjaxRequest: function() {
        var ajax = null;
        ajax = new XMLHttpRequest;
        return ajax;
    },
    minusIfNull: function(input) {
        return (input == null || input == "") ? "-" : input;
    },
    getDayName: function(number) {
        switch(number) {
            case 0:
                return "Sonntag";
            case 1:
                return "Montag";
            case 2:
                return "Dienstag";
            case 3:
                return "Mittwoch";
            case 4:
                return "Donnerstag";
            case 5:
                return "Freitag";
            case 6:
                return "Samstag";
        }
    },
    getMonthName: function(number) {
        switch(number) {
            case 0:
                return "Januar";
            case 1:
                return "Februar";
            case 2:
                return "Mrz";
            case 3:
                return "April";
            case 4:
                return "Mai";
            case 5:
                return "Juni";
            case 6:
                return "Juli";
            case 7:
                return "August";
            case 8:
                return "September";
            case 9:
                return "Oktober";
            case 10:
                return "November";
            case 11:
                return "Dezember";
        }
    },
    isInteger: function(input) {
        var regex = /^-?[0-9]*[1-9][0-9]*$/;
        return regex.test(input);
    },
    niceifyTimestamp: function(input) {
        let date = new Date(input);
        return date.getDate() + "." + (date.getMonth() + 1) + "." + date.getFullYear() + " " + date.getHour() + ":" + date.getMinute() + ":" + date.getSeconds();
    },
    getLogText: function(type, info) {
        switch(type) {
            case 0:
                switch(info) {
                    case 0:
                        return "Erfolgreich angemeldet.";
                    case 10:
                        return "Zugangsdaten fehlerhaft.";
                    case 11:
                        return "Berechtigungen fehlerhaft.";
                }
            case 1:
                switch(info) {
                    case 0:
                        return "Passwort erfolgreich gendert.";
                    case 1:
                        return "Passwort ndern fehlgeschlagen: Datenbankfehler.";
                    case 2:
                        return "Passwort ndern fehlgeschlagen: LDAP-Fehler.";
                    case 10:
                        return "Passwort ndern fehlgeschlagen: Altes Passwort falsch.";
                    case 11:
                        return "Passwort ndern fehlgeschlagen: Neue Passwrter stimmen nicht berein.";
                }
            case 2:
                switch(info) {
                    case 0:
                        return "Passwort erfolgreich zurckgesetzt.";
                    case 1:
                        return "Passwort zurcksetzen fehlgeschlagen: Datenbankfehler.";
                    case 2:
                        return "Passwort zurcksetzen fehlgeschlagen: LDAP-Fehler.";
                    case 10:
                        return "Passwort zurcksetzen fehlgeschlagen: Authentifizierung abgelehnt.";
                    case 11:
                        return "Passwort zurcksetzen fehlgeschlagen: Neue Passwrter stimmen nicht berein.";
                    case 12:
                        return "Passwort zurcksetzen fehlgeschlagen: Berechtigung fehlt.";
                    case 13:
                        return "Passwort zurcksetzen fehlgeschlagen: Passwort des Ziels kann nicht zurckgesetzt werden.";
                    case 14:
                        return "Passwort zurcksetzen fehlgeschlagen: Kein Lehrer-PC.";
                }
        }
        return "";
    }
}

export default essentials;
